/* Nunito Font */
@font-face {
    font-family: 'Nunito';
    src: url('data:application/font-woff2;charset=utf-8;base64,')
            format('woff2'),
        url('data:application/font-woff;charset=utf-8;base64,')
            format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

