.canvas {
    align-items: center;
    background: #eeeeee;
    border-radius: 50%;
    box-shadow: 0 5px 20px rgba(0, 0, 0, 0.2);
    display: flex;
    height: 23em;
    justify-content: center;
    margin: 1em 1em 2em 1em;
    width: 23em;
}
.canvas2 {
    position: relative;
}

.spinner2 {
    animation: spinnerTwo 1s linear infinite;
    background: #161818;
    border-radius: 100px;
    height: 6.9em;
    transform-origin: top;
    position: absolute;
    top: 50%;
    width: 0.506em;
}

.hourHand {
    animation: spinnerTwo 7s linear infinite;
    background: #000000;
    border-radius: 100px;
    height: 4.6em;
    transform-origin: top;
    position: absolute;
    top: 50%;
    width: 0.46em;
}

.dot {
    background: #380052;
    border-radius: 100%;
    height: 1.15em;
    width: 1.15em;
}

@keyframes spinnerTwo {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

